* {
  font-family: 'Roboto', sans-serif; }

header {
  min-height: 50px;
  background-color: #212121;
  position: fixed;
  z-index: 1000;
  width: 100%; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    margin-top: 16px;
    max-width: 100%; }

  .subheader_official {
    margin-top: 6px;
    color: #ffffff;
    font-weight: 400;
    font-size: 13px;
    opacity: 0.5; }

  .subheader_call {
    margin-top: 19px;
    color: #ffffff;
    font-weight: 300;
    font-size: 12px;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase; } }

.promo {
  min-height: 800px;
  background: url('../image/img/mainscreen.bg.png') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  .promo_subheader {
    margin-top: 22px;
    color: #ffffff;
    font-weight: 700;
    font-size: 36;
    line-height: 36px;
    margin: 0;
    text-align: center; }

  .promo_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase; } }

.reasons {
  min-height: 765px; }

.label {
  margin: 0 auto;
  margin-top: 70px;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center; }

.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }
.subtitle {
	margin-top: 19px;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center; }


.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; }

    .mobile {
        min-height: 864px;
        padding: 48px 0 79px 0;
        background: url ('../image/img/mobile.bg.png') center (center / cover) no-repeat;
        &_item {
            width: 320px;
            height: 280px;
            margin: 0 auto;
            &_1 {
                background-image: url ('../image/img/mobile.card1.jpg') center (center / cover) no-repeat; }
            &_2 {
                background: url ('../image/img/mobile.card2.jpg') center (center / cover) no-repeat; }
            &_3 {
                background: url ('../image/img/mobile.card3.jpg') center (center / cover) no-repeat; }
            &_4 {
                background: url ('../image/img/mobile.card4.jpg') center (center / cover) no-repeat; }
            &_5 {
                background: url ('../image/img/mobile.card5.jpg') center (center / cover) no-repeat; }
            &_6 {
                background: url ('../image/img/mobile.card6.jpg') center (center / cover) no-repeat; } } } } }
